import { Base64 } from 'js-base64';
/**
 * @description url(base64)base64
 * @example analysisQuery('TM1MDAwMDAwMDA=');
 * @param {String} queryString
 * @return {String} 
 */
export function analysisQuery(queryString) {
	if(queryString) {
		try {
			queryString = Base64.encode(queryString);
		} catch(e) {
			console.error(e);
			return undefined;
		};
		return queryString;
	} else {
		return queryString;
	};
};

export function getQuerys(route, obj) {
	var props = {};
	Object.entries(obj).forEach(function(v, i) {
		props[v[0]] = analysisQuery(route.query[v[1]]);
	});
	console.log(props);
	return props;
};

/**
 * @description script
 * @param {String} url
 * @return {Promise}
 */
export function loadScript(url) {
	const s = window.document.createElement('script');
	s.src = url;
	return new Promise(function(resolve, reject) {
		document.head.appendChild(s);
		s.onload = resolve;
		s.onerror = reject;
	});
};

/**
 * @description Iphone
 * @return {Boolean}
 */
export function isIphone() {
	var ua = window.navigator.userAgent.toLowerCase();
	var isIph = ua.indexOf('iphone') > -1;
	if(isIph) {
		return true;
	}
	return false;
};

